.container{
  text-align: center;

}
h1{margin: 5px;padding: 0;}

.board{
  margin: auto;
  width: 300px;
  height: 300px;
  background-color: darkgrey;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: 1fr 1fr 1fr;
}

.reset{
  background-color: white;
  border-radius: 10px;
  width: 300px;
  padding:10px;
  font-size:1.25rem;
  color: black;
  font-weight: bold;
  margin-top: 10px;
}
.status{
  margin-bottom: 10px;
}
.X{
background-color: rgb(27, 177, 27);
  font-size: larger;
  color: black;
}
.O{
background-color: rgb(235, 64, 64);
font-size: larger;
color: black;
}